
@import "../../../MediaQuery/Mixin.scss";
.grid-card{

    display: grid;
    grid-template-columns: auto;
    justify-content: center;
    gap: 10px;
    margin: 40px 0;
    padding: 10px;

    @include m{
        grid-template-columns: auto auto auto;
    }

   


    .card{

        height: 250px;
        width: 250px;
        padding: 20px;
        background-color: cadetblue;
       


      

    }
}