@import "../MediaQuery/Mixin.scss";

.header-main{
    background-color: #403a30;
  
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 30px;
    color: white;
    

 



    .left{
        width: 80px;
        height: 40px;

        @include head{
            width: 100px;
            height: 50px;
            cursor: pointer;
        }

        img{
            height: 100%;
            width: 100%;
        }

      
    }

    .center{
        list-style: none;
       
        gap: 40px;
        font-size: 18px;
        display: none;


        @include head{
            display: flex;
            cursor: pointer;
           


            li{

                transition: transform 0.5s;
                &:hover{
                    transform: scale(1.2);
                }

                &:nth-child(2){
                    color: rgb(235, 14, 51);
                    background-color: white;
                    padding: 4px;
                    border-radius: 4px 10px 7px 9px;
                }
            }

            
        }
    }

    .right{
        display: flex;
        gap: 20px;
        

        svg{
            font-size: 17px;
        }

        @include head{
            
            svg{
                font-size: 22px;
                cursor: pointer;
                
            }
        }
    }
}