.dropdown-main{

    display: flex;
    justify-content: center;
    margin: 30px 0;
    position: relative;


    button{
      padding: 10px 20px;
      background-color: cadetblue;
      border-radius: 10px;
      border: none;
      cursor: pointer;
    }

    .dropdown-content{
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        list-style: none;
        border-radius: 5px;
        border-right: 5px solid greenyellow;
        position: absolute;
        top: 50px;
        z-index: 9999;
        background-color: white;
        animation: drop 0.5s;
        

        li{
            padding: 10px 20px;
            cursor: pointer;

            &:hover{
                background-color: greenyellow;
            }
        }
    }
}



@keyframes drop{
    0%{transform: scale(0);}
    100%{transform: scale(1);}
}